* {
  box-sizing: border-box;
}
a,
img,
button,
input,
textarea {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
/* header-common */
.wrap {
  position: relative;
  min-height: 100%;
}
.header {
  height: 42px;
  width: 100%;
  background: #4f68ff;
  text-align: center;
  position: relative;
}
.header .icon-back {
  position: absolute;
  width: 12px;
  height: 19px;
  background: url(../images/icon_back.png) no-repeat center center;
  background-size: cover;
  top: 12px;
  left: 16px;
}
.header .icon-close {
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  background: url(../images/head_close.png) no-repeat center center;
  background-size: cover;
  top: 11px;
  right: 12px;
}
.header span {
  font-size: 16px;
  color: #fff;
  line-height: 42px;
  display: block;
}
/*detail*/
.borrow-detail {
  padding-bottom: 1.1rem;
}
.borrow-detail .title {
  height: 2.35rem;
  background-color: #f5f5f5;
  font-size: 0.8rem;
  color: #333333;
  line-height: 2.35rem;
  padding-left: 0.8rem;
}
.borrow-detail .info-block li {
  padding: 0.85rem 0.8rem;
  background-color: #ffffff;
  border-bottom: 1px solid #d8d8d8;
  line-height: 0.9rem;
}
.borrow-detail .info-title,
.borrow-detail .info-txt {
  font-size: 0.7rem;
  color: #333333;
}
.borrow-detail .info-title {
  float: left;
}
.borrow-detail .info-txt {
  float: right;
}
.borrow-detail .info-txt span {
  color: #999999;
}
.borrow-detail .info-txt a {
  color: #50abff;
}
.borrow-detail .progress-block {
  background-color: #ffffff;
  padding: 0.75rem 0.65rem;
}
.borrow-detail .progress-block li {
  display: block;
  width: 100%;
  padding-bottom: 20px;
  position: relative;
}
.borrow-detail .progress-block li:after {
  position: absolute;
  display: block;
  content: '';
  width: 1px;
  height: 24px;
  bottom: -4px;
  left: 8px;
  background: #cbcbcb;
}
.borrow-detail .progress-block li.line-active:after {
  background: #50abff;
}
.borrow-detail .progress-block li.active .progress-icon {
  background: url(../images/progress_blue.png) no-repeat center center;
  background-size: cover;
}
.borrow-detail .progress-block li.active .progress-info {
  color: #363636;
}
.borrow-detail .progress-block li.active .progress-info:after {
  background: #50abff;
}
.borrow-detail .progress-block li:last-child {
  padding-bottom: 0;
}
.borrow-detail .progress-block li:last-child:after {
  display: none;
}
.borrow-detail .progress-block li:last-child .progress-info:after {
  display: none;
}
.borrow-detail .progress-info {
  color: #b5b5b5;
  padding: 0 1rem 0 1.25rem;
  position: relative;
}
.borrow-detail .progress-info:after {
  position: absolute;
  display: block;
  content: '';
  width: 1px;
  height: calc(100% - 20px);
  background: #cbcbcb;
  left: 8px;
  top: 20px;
}
.borrow-detail .progress-info .p-title {
  font-size: 0.75rem;
  display: inline-block;
  height: 22px;
  line-height: 22px;
  overflow: hidden;
}
.borrow-detail .progress-info .p-time {
  font-size: 0.55rem;
  display: inline-block;
  vertical-align: bottom;
  height: 22px;
  line-height: 22px;
  overflow: hidden;
}
.borrow-detail .progress-info .subtext {
  font-size: 0.6rem;
  line-height: 18px;
}
.borrow-detail .progress-info .progress-icon {
  position: absolute;
  top: 4px;
  left: 0;
  display: block;
  width: 17px;
  height: 17px;
  background: url(../images/progress_grey.png) no-repeat center center;
  background-size: cover;
}
.grey-tip {
  line-height: 1rem;
  color: #999;
  font-size: 0.7rem;
  padding: 0.45rem 0.65rem 0.2rem;
}
.from-wrapper {
  background: #fff;
}
.input-line {
  height: 50px;
  line-height: 50px;
  padding: 0 14px;
  overflow: hidden;
  border-bottom: 1px solid #d8d8d8;
  position: relative;
}
.input-line span.l {
