  text-align: center;
  font-size: 14px;
  color: #50abff;
  margin: 1rem 0 0;
  padding: 0.5rem 0;
}
.overdue-tips {
  color: #999;
  font-size: 14px;
  text-align: center;
}
.coupon-modal {
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  overflow: hidden;
}
.coupon-modal .coupon-con {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  background: #fff;
}
.coupon-modal .coupon-close {
  height: 32px;
  line-height: 32px;
  text-align: right;
  font-size: 16px;
  color: #4a9bee;
  overflow: hidden;
  border-bottom: 1px solid #e7e7e7;
  padding: 0 4%;
}
.coupon-modal dl {
  padding: 0.5rem 0;
  color: #333;
}
.coupon-modal dl dt {
  font-size: 14px;
  color: #717171;
  height: 30px;
  line-height: 30px;
  overflow: hidden;
  border-bottom: 1px dashed #d2d2d2;
  margin-bottom: 0.5rem;
  padding: 0 4%;
}
.coupon-modal dl dd {
  font-size: 16px;
  overflow: hidden;
  padding: 0.4rem 4%;
}
.coupon-modal dl dd.active {
