}
.coupon-modal dl dd em {
  display: block;
  float: left;
}
.coupon-modal dl dd p {
  display: block;
  float: right;
}
.coupon-modal dl.unusable {
  color: #a8a8a8;
}
.coupon-modal dl.unusable dt {
  color: #a8a8a8;
}
.overdue-modal {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  overflow: hidden;
}
.overdue-modal .overdue-con {
