}
.overdue-modal .overdue-con .overdue-line {
  margin-bottom: 0.75rem;
}
.overdue-modal .overdue-con span {
  font-size: 16px;
  display: inline-block;
  color: #333;
}
.overdue-modal .overdue-con span.fl {
