}
.overdue-modal .overdue-con .overdue-close {
  display: block;
  height: 44px;
  line-height: 44px;
  margin: 1.25rem 0.7rem 0;
  font-size: 18px;
  text-align: center;
  border-radius: 50px;
  background: #4f68ff;
  color: #fff;
}
.text-6 {
  font-size: 14px;
  color: #666;
  text-align: center;
  padding: 0.75rem 0;
  background-color: #fff;
}
.money-num {
  color: #666;
  font-size: 14px;
  text-align: center;
  background-color: #fff;
  padding-bottom: 0.5rem;
}
.money-num em {
  display: inline-block;
}
.money-num span {
  display: inline-block;
  color: #333;
  font-size: 30px;
}
.bank-pay {
  padding: 1.1rem 0.8rem;
  border-top: 1px solid #d8d8d8;
  background-color: #fff;
}
.bank-pay .bank-logo {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
}
.bank-pay span {
  display: inline-block;
