  font-size: 0.8rem;
  padding: 0 2.5rem 2rem;
  line-height: 1.2rem;
}
.pay-success .success-tips {
  font-size: 0.7rem;
}
*[v-cloak] {
  display: none;
}
.a-btn a.redColor {
  background: #FF4542;
}
.password-body .password-title.redColor {
  background: #FF4542;
}
.a-btn.redColor a {
  background: #FF4542;
}
.a-btn.redColor a.grey-bg {
  background: #ccc;
}



